.DatePicker{
    font-family: inherit;
  width: 100%;
  height: 3em;
  border: 0;
  border: 1px solid #807D7B;
  border-radius: 12px;
  outline: 0;
  font-size: 16px;
  color: #212121;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.DatePicker::placeholder {
    color: transparent;
  }
  .DatePicker:focus {
    padding-bottom: 6px;
    border: 2px solid #807D7B;
  }